<template>
    <div>
        <ul class="plugin-list">
            <li>
                <h3>Name：</h3>
                <div class="text">ActionList</div>
            </li>
            <li>
                <h3>Introduction：</h3>
                <div class="text">操作列表, 由移动端底部弹出</div>
            </li>
            <li>
                <h3>Demo：</h3>
                <p class="btn" @click.stop="onClickDemo()">demo</p>
                <div class="text">
                    您选择了：<input type="text" readonly="readonly" v-model="nowVal">
                </div>
            </li>
            <li>
                <h3>Function：</h3>
<pre class="JS">
mobileModal.alert(
    '提示内容',//必填

    //回调函数(可选)
    function () {
        self.nowVal = '确定按钮';
    }
);
</pre>
            </li>
            <li>
                <h3>Usage：</h3>
                <div class="text">
                    引入文件：</br>
                    'mobileModal/mobileModal.css'</br>
                    'mobileModal/mobileModal.js'
                </div>
            </li>
        </ul>


        <!--<div class="zx-mask action-list">-->
            <!--<div>-->
                <!--<div>-->
                    <!--<header>确认关闭吗?</header>-->
                    <!--<ul>-->
                        <!--<li>选项一</li>-->
                        <!--<li>选项二</li>-->
                        <!--<li>选项二</li>-->
                    <!--</ul>-->
                <!--</div>-->
                <!--<p>取消</p>-->
            <!--</div>-->
        <!--</div>-->

    </div>
</template>

<script>
    import mobileModal from '../../lib/mobileModal/mobileModal.js'
    export default {
		name: 'alert',
        data(){
            return {
                nowVal: ''
            }
        },
        methods: {
            onClickDemo(){
                var self = this;
                mobileModal.showActionList({
                    title: '',
                    button: '取消',
                    data: [
                        '选项一', '选项二', '选项三'
                    ],
                    callback: function (val) {
                        self.nowVal = val;
                    }
                });
            }
        }
	}
</script>

